.list-page {
  color: #757575;
  font-size: 12px;
  height: 35px;
  line-height: 35px;
  position: initial;
  bottom: 0;
  background: rgba(255,255,255,1);
  box-shadow: 0px 0px 5px 0px rgba(145,145,145,0.5);
  border-radius: 4px;
  margin: 0;
  padding: 0 20px;
  width: 1200px;
  z-index: 2;
  .select-page-num {
    margin-right: 20px;
    float: left;
    select {
      display: inline-block;
      vertical-align: middle;
      width: 56px;
      height: 28px;
      color: #f99429;
      padding: 6px 5px;
      border-radius: 4px;
      transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
      box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
      background: #fff;
      border: 1px solid #e9e9e9;
      font-size: 12px;
    }
  }
  .total-num {
    float: left;
    min-width: 120px;
    margin-right: 20px;
    margin-left: 195px;
  }
  .page-index {
    min-width: 330px;
    float: left;
    a {
      float: left;
      text-align: center;
      color: #8d9da5;
      min-width: 24px;
      height: 24px;
      /* background: rgba(255,119,0,1); */
      margin: 6px 5px;
      line-height: 24px;
      padding: 0 4px;
      &.current {
        color: #fff;
        background: rgba(255,119,0,1);
      }
    }
  }
  .to-go {
    float: left;
    width: 250px;
    text-align: right;
    input {
      display: inline-block;
      width: 48px;
      height: 28px;
      border-color: #e9e9e9;
      vertical-align: middle;
    }
    a {
      display: inline-block;
      width: 40px;
      height: 28px;
      line-height: 28px;
      padding: 0;
      background-color: #f8f8f8;
      border-color: #e5e1da;
      color: #f99429;
      margin-left: 20px;
      font-size: 12px;
    }
  }
}